export const LETTER_LIST = 'ABCDEFGHIJKLMNOPQRSTUVWXYZ';

export default function arabic2Letter(number: number, type: string): string {
  let num = number;
  const map = type === 'upper' ? LETTER_LIST : LETTER_LIST.toLowerCase();
  let result = '';
  while (num > 0) {
    num -= 1;
    result = map[num % 26] + result;
    num = Math.floor(num / 26);
  }
  return result;
}
